/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import './Inventory.css';
import { Select, Button, Dropdown, Menu, Icon, Modal, Form, Input, DatePicker, notification, Popover } from 'antd';
import MyInput from '../shopper/CustomInput'
import moment from 'moment';
import { recipeIcon, plannerIcon } from '../AppHeader'

const { RangePicker } = DatePicker

export default class extends React.Component {
    state = {

    }
    findIngredientsUsedInEvents() {
        const { foodnet } = window;
        let list = [];
        const now = moment().startOf('day');
        const plannedDate = moment().endOf('day').add(foodnet.listDays - 1, 'days');
        foodnet.events.filter(e => moment(e.date).isBetween(now, plannedDate)).forEach(event => {
            const multiplier = Number(event.qty) || 0;
            const recipe = foodnet.recipes.find(r => r.id === event.recipe);
            recipe.ingredients.forEach(ing => {
                list.push({ id: ing.id, type: ing.type, qty: Number(ing.qty) * multiplier });
            })
        })

        const mergeDuplicates = (item, index, l) => {
            if (index > 0 && l[index - 1].id === item.id && l[index - 1].type === item.type) {
                let newIndex = index - 1;
                while (l[newIndex].deleted) newIndex--;
                l[newIndex].qty += item.qty;
                item.deleted = true;
            }
            return item;
        };
        list = list.sort((a, b) => {
            if (a.id !== b.id) {
                return a.id < b.id ? -1 : 1;
            }
            if (a.type !== b.type) {
                return a.type < b.type ? -1 : 1;
            }
            return 0;
        }).map(mergeDuplicates).filter(item => !item.deleted);

        return (
            list.map(item => ({
                ...item,
                name: foodnet.ingredients.find(i => i.id === item.id).name,
                typename: foodnet.measurements.find(m => m.id === item.type).name,
            }))
                .sort((a, b) => a.name.localeCompare(b.name) || a.typename.localeCompare(b.typename))
        );
    }
    renderToolbar() {
        const menuOnClickHandler = (key) => {
            this.setState({ modal: key });
        }
        const createMenu = (type) => (
            <Menu className={`inventory-toolbar-dropdown-${type}`} onClick={e => menuOnClickHandler(e.key)}>
                <Menu.Item key={`${type}-recipe`}>
                    {recipeIcon}
                    Recipe
                </Menu.Item>
                <Menu.Item key={`${type}-event`}>
                    {plannerIcon}
                    Event dates
                </Menu.Item>
            </Menu>
        );
        return (
            <div className="inventory-toolbar">
                <Dropdown overlay={createMenu('add')}>
                    <Button>
                        Add from ...<Icon type="down" />
                    </Button>
                </Dropdown>
                <Dropdown overlay={createMenu('remove')}>
                    <Button>
                        Deduct from ...<Icon type="down" />
                    </Button>
                </Dropdown>
            </div>
        );
    }
    renderModals() {
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 12 },
            },
        };
        const getEvents = () => {
            const { foodnet } = window;
            let elist = []
            if (this.state.dates) {
                foodnet.events.forEach(event => {
                    let ilist = []
                    if (moment(event.date).isBetween(...this.state.dates, 'days', '[]')) {
                        const multiplier = Number(event.qty) || 0;
                        const recipe = foodnet.recipes.find(r => r.id === event.recipe);
                        recipe.ingredients.forEach(ing => {
                            ilist.push({ id: ing.id, type: ing.type, qty: Number(ing.qty) * multiplier, name: foodnet.ingredients.find(it => it.id === ing.id).name });
                        })
                        elist.push({
                            event: event,
                            ilist: ilist,
                            recipe: recipe
                        })
                    }
                })
            }
            return elist.sort((a, b) => {
                const aDate = moment(a.event.date).startOf('day')
                const bDate = moment(b.event.date).startOf('day')
                if (aDate.isBefore(bDate)) return -1
                if (aDate.isAfter(bDate)) return 1
                return a.recipe.name.localeCompare(b.recipe.name)
            })
        }
        const valid = () => {
            const { modal } = this.state;
            if (modal === 'add-recipe' || modal === 'remove-recipe') {
                return this.state.recipe && this.state.recipe > 0;
            }
            else {
                return this.state.dates && this.state.dates.length > 0 && getEvents().length > 0;
            }
        }
        const submit = () => {
            const { modal } = this.state;
            const { foodnet } = window;
            if (modal === 'add-recipe' || modal === 'remove-recipe') {
                const r = foodnet.recipes.find(r => r.id === this.state.recipe)
                r.ingredients.forEach(ing => {
                    let inv = foodnet.ingredients.find(it => it.id === ing.id)
                    if (!inv.type) {
                        inv.type = ing.type;
                    }
                    if (inv.type !== ing.type) {
                        notification.error({ message: "Error: Measurement type mismatch", description: "Recipe: " + r.name + ", Ingredient: " + ing.name, duration: 0 });
                    }
                    else {
                        const multiplier = this.state.qty;
                        if (inv.qty === undefined) inv.qty = 0
                        inv.qty = Number(inv.qty)
                        if (modal === 'add-recipe') inv.qty += Number(ing.qty || 1) * Number(multiplier || 1);
                        else {
                            inv.qty -= Number(ing.qty || 1) * Number(multiplier || 1);
                            if (inv.qty < 0) inv.qty = 0
                        }
                        foodnet.sync()
                    }
                })
            }
            else if (modal === 'add-event' || modal === 'remove-event') {
                getEvents().forEach(e => {
                    const r = e.recipe
                    e.ilist.forEach(ing => {
                        let inv = foodnet.ingredients.find(it => it.id === ing.id)
                        if (!inv.type) {
                            inv.type = ing.type;
                        }
                        if (inv.type !== ing.type) {
                            notification.error({ message: "Error: Measurement type mismatch", description: "Recipe: " + r.name + ", Ingredient: " + ing.name, duration: 0 });
                        }
                        else {
                            if (inv.qty === undefined) inv.qty = 0
                            inv.qty = Number(inv.qty)
                            if (modal === 'add-event') inv.qty += Number(ing.qty || 1);
                            else {
                                inv.qty -= Number(ing.qty || 1);
                                if (inv.qty < 0) inv.qty = 0
                            }
                            foodnet.sync()
                        }
                    })
                })
            }
            this.setState({ modal: undefined, recipe: undefined, qty: undefined, dates: undefined })
        }
        const cancel = () => {
            this.setState({ modal: undefined, recipe: undefined, qty: undefined, dates: undefined })
        }
        const createEventCard = (e) => {
            return (
                e.ilist.map((i, index) => (
                    <div key={index}>{`${i.name} - ${i.qty} ${window.foodnet.measurements.find(it => it.id === i.type).name}`}</div>
                ))
            )
        }
        return (
            <>
                {(this.state.modal === 'add-recipe' || this.state.modal === 'remove-recipe') && (
                    <Modal width="90%" visible={true} keyboard={true}
                        onCancel={() => cancel()}
                        onOk={() => submit()}
                        okButtonProps={{ disabled: !valid() }}
                    >
                        <Form {...formItemLayout}>
                            <Form.Item validateStatus={valid() ? "success" : "error"} hasFeedback={true} label="Recipe">
                                <Select value={this.state.recipe} onChange={v => this.setState({ recipe: Number(v) })}>
                                    {window.foodnet.recipes.map(r => (
                                        <Select.Option key={r.id} value={r.id}>{r.name}</Select.Option>
                                    ))}
                                </Select>
                            </Form.Item>
                            <Form.Item label="Quantity">
                                <Input
                                    autoFocus
                                    type="number"
                                    defaultValue={1}
                                    value={this.state.qty}
                                    onChange={e => this.setState({ qty: e.target.value })}
                                    onPressEnter={() => {
                                        if (valid()) {
                                            submit();
                                        }
                                    }}
                                />
                            </Form.Item>
                        </Form>
                    </Modal>)}
                {(this.state.modal === 'add-event' || this.state.modal === 'remove-event') && (
                    <Modal width="90%" visible={true} keyboard={true}
                        onCancel={() => cancel()}
                        onOk={() => submit()}
                        okButtonProps={{ disabled: !valid() }}
                    >
                        <Form {...formItemLayout}>
                            <Form.Item validateStatus={valid() ? "success" : "error"} hasFeedback={true} label="Event Dates">
                                <RangePicker value={this.state.dates} onChange={(m) => {
                                    this.setState({ dates: m })
                                }} />
                            </Form.Item>
                            <Form.Item label={`Events to ${this.state.modal === 'add-event' ? 'add' : 'remove'}`}>
                                {getEvents().map(e => (
                                    <div key={e.event.id}>
                                        {`${e.event.qty} x `}
                                        <Popover content={createEventCard(e)}>
                                            <a onClick={() => {}}>{`${e.recipe.name}`}</a>
                                        </Popover>
                                        {` on ${moment(e.event.date).format("MMM Do")}`}
                                    </div>
                                ))}
                            </Form.Item>
                        </Form>
                    </Modal>)}
            </>
        )
    }
    render() {
        const { foodnet } = window;
        const onDropdownChange = (v, item) => {
            item.type = v;
            foodnet.sync();
            this.forceUpdate()
        }
        const createDropdown = (item) => (
            <Select className="inventory-list-item-type" value={item.type} onChange={v => onDropdownChange(v, item)}>
                {foodnet.measurements.map(m => (
                    <Select.Option key={m.id} value={m.id}>{m.name}</Select.Option>
                ))}
            </Select>
        )
        const isEditing = (item) => this.state.edit === item.id;
        const readOnlyClickHandler = (item, e) => this.setState({ edit: item.id })
        const onInputChange = (v, item) => {
            item.qty = v;
            foodnet.sync();
            this.forceUpdate()
        }
        const eventUsedIngredients = this.findIngredientsUsedInEvents();
        const used = (ing) => {
            const uing = eventUsedIngredients.find(u => u.id === ing.id && u.type === ing.type);
            if (!uing) return '';
            return ing.qty < uing.qty ? ' used-short' : ' used-good';
        }
        const createItem = (item, index) => (
            isEditing(item) ?
                <React.Fragment key={index}>
                    <div className={"inventory-list-item-name" + used(item)} onClick={() => this.setState({ edit: undefined })}>{item.name}</div>
                    <MyInput className="inventory-list-item-qty" type="number" min="0" max="9999" defaultValue={item.qty} onPressEnter={(e) => onInputChange(e.target.value, item)} onBlur={(e) => onInputChange(e.target.value, item)} />
                    {createDropdown(item)}
                    <div className="inventory-list-item-deselect-div" onClick={() => this.setState({ edit: undefined })} />
                </React.Fragment>
                :
                <React.Fragment key={index}>
                    <div className={'inventory-list-item-name' + used(item)} onClick={(e) => readOnlyClickHandler(item, e)}>{item.name}</div>
                    <div className={'inventory-list-item-qty' + used(item)} onClick={(e) => readOnlyClickHandler(item, e)}>{item.qty || 0}</div>
                    <div className={'inventory-list-item-type' + used(item)} onClick={(e) => readOnlyClickHandler(item, e)}>{item.type ? foodnet.measurements.find(m => m.id === item.type).name : "(no measurement type)"}</div>
                    <div className={'inventory-list-item-deselect-div'} onClick={() => this.setState({ edit: undefined })} />
                </React.Fragment>
        );
        return (
            <div className="inventory">
                {this.renderToolbar()}
                <div className="inventory-list">
                    {foodnet.ingredients.map(createItem)}
                </div>
                {this.renderModals()}
            </div>
        );
    }
}